    //     font-size: 0.8rem;
    //     font-weight: bold;
    //     // font-style: oblique;
    //     &:hover{
    //         color:#525E75 ;
    //     }
    // }
    input{
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: transparent;
        width: 1.2rem;
        height: 1.2rem;
        border: none;
        cursor: pointer;
        border-radius: 50%;
        border: 2px solid #000000;
        color: transparent;
    }
} 

