/* ESTILO DEL DIV QUE CONTENTRÁ EL INPUT */
.upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: inline-block;
    }
    /* ESTILO DEL INPUT*/
    .upload-btn-wrapper input[type=file] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    }
    /* ESTILO DEL BOTÓN */
    .boton-standar-rw{
    width: 7.5rem;
    height: 7.5rem;
    border-radius: 4rem;
    object-fit: cover;
    border: 0.2rem solid #8CB2C4;
    }
    /* ESTILO DEL BOTÓN CON HOVER DE MOUSE */
    .boton-standar-rw:hover{
      color: white;
      background-color: rgb(245, 61, 58);
    }

