//check success icon
$green:     #4cc93f;
$icon-base-size: 4px;

// Structure
.success-icon {
  // display: flex;
  width: 40px;
  height: 40px;
  font-size: $icon-base-size;
  margin-left: -2.5rem;
  // border-radius: 50%;
  // border: 4px solid lighten($green, 20%);
  // background-color: #fff;
  // position: absolute;
  overflow: hidden;
  transform-origin: center;
  animation: showSuccess 180ms ease-in-out;
  transform: scale(1);
}

// Elements
.success-icon {
  
  &__tip,
  &__long {
    display: block;
    position: absolute;
    height: 1.9px;
    background-color: lighten($green, 20%);
    // border-radius: 10px;
  }

  &__tip {
    width: 2.4em;
    // top: 4.3em;
    // left: 1.4em;
    transform: rotate(45deg);
    animation: tipInPlace 300ms ease-in-out;
    animation-fill-mode: forwards;
    animation-delay: 180ms;
    visibility: hidden;
  }

  &__long {
    // width: 4em;
    transform: rotate(-45deg);
    // top: 3.70em;
    // left: 2.75em;
    animation: longInPlace 140ms ease-in-out;
    animation-fill-mode: forwards;
    visibility: hidden;
    animation-delay: 300ms + 140ms;
  }

}

@keyframes showSuccess {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

@keyframes tipInPlace {
    from {
        width: 0em;
        top: 0em;
        left: -1.6em;
    }
    to {
        width: 2.4em;
        top: 4.3em;
        left: 1.4em;
        visibility: visible;
    }
}

@keyframes longInPlace {
    from {
        width: 0em;
        top: 5.1em;
        left: 3.2em;
    }
    to {
        width: 4em;
        top: 3.70em;
        left: 2.75em;
        visibility: visible;
    }
}
